import React, { useState } from "react";
import { Project } from "@/app/lib/api";
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
} from "@/app/components/ui/card";
import { Badge } from "@/app/components/ui/badge";
import { Button } from "@/app/components/ui/button";
import { ProjectDetailDialog } from "./ProjectDetailDialog";
import {
  Calendar,
  CheckCircle,
  Clock,
  MoreHorizontal,
  Users,
} from "lucide-react";

interface ProjectCardProps {
  project: Project;
}

export const ProjectCard: React.FC<ProjectCardProps> = ({ project }) => {
  const [isDetailOpen, setIsDetailOpen] = useState(false);

  const completedTasks = project.tasks.filter(
    (task) => task.status === "completed"
  ).length;
  const totalTasks = project.tasks.length;
  const progressPercentage =
    totalTasks > 0 ? (completedTasks / totalTasks) * 100 : 0;

  const getStatusColor = (status: string) => {
    switch (status) {
      case "completed":
        return "bg-green-100 text-green-800 border-green-200";
      case "in-progress":
        return "bg-orange-100 text-orange-800 border-orange-200";
      case "todo":
        return "bg-blue-100 text-blue-800 border-blue-200";
      default:
        return "bg-slate-100 text-slate-800 border-slate-200";
    }
  };

  return (
    <>
      <Card
        className="group hover:shadow-lg transition-all duration-200 cursor-pointer border-0 shadow-sm bg-white hover:shadow-xl hover:-translate-y-1"
        onClick={() => setIsDetailOpen(true)}
      >
        <CardHeader className="pb-4">
          <div className="flex items-start justify-between">
            <div className="space-y-2">
              <CardTitle className="text-lg font-semibold text-slate-800 group-hover:text-primary transition-colors">
                {project.title}
              </CardTitle>
              <p className="text-sm text-slate-600 line-clamp-2">
                {project.description}
              </p>
            </div>
            <Button
              variant="ghost"
              size="sm"
              className="opacity-0 group-hover:opacity-100 transition-opacity"
            >
              <MoreHorizontal className="h-4 w-4" />
            </Button>
          </div>
        </CardHeader>

        <CardContent className="space-y-4">
          {/* Progress Bar */}
          <div className="space-y-2">
            <div className="flex items-center justify-between text-sm">
              <span className="text-slate-600">Progression</span>
              <span className="font-medium text-slate-800">
                {completedTasks}/{totalTasks} tâches
              </span>
            </div>
            <div className="w-full bg-slate-100 rounded-full h-2 overflow-hidden">
              <div
                className="bg-gradient-to-r from-primary to-primary/80 h-2 rounded-full transition-all duration-300"
                style={{ width: `${progressPercentage}%` }}
              />
            </div>
          </div>

          {/* Task Status Summary */}
          <div className="flex flex-wrap gap-1">
            {project.tasks.slice(0, 3).map((task) => (
              <Badge
                key={task.id}
                variant="outline"
                className={`text-xs ${getStatusColor(task.status)}`}
              >
                {task.status === "completed" && (
                  <CheckCircle className="h-3 w-3 mr-1" />
                )}
                {task.status === "in-progress" && (
                  <Clock className="h-3 w-3 mr-1" />
                )}
                {task.title.length > 15
                  ? `${task.title.substring(0, 15)}...`
                  : task.title}
              </Badge>
            ))}
            {project.tasks.length > 3 && (
              <Badge
                variant="outline"
                className="text-xs bg-slate-50 text-slate-600"
              >
                +{project.tasks.length - 3} autres
              </Badge>
            )}
          </div>

          {/* Project Meta */}
          <div className="flex items-center justify-between pt-2 border-t border-slate-100">
            <div className="flex items-center gap-1 text-xs text-slate-500">
              <Calendar className="h-3 w-3" />
              {new Date(project.createdAt).toLocaleDateString("fr-FR")}
            </div>
            <Badge
              variant="secondary"
              className="bg-primary/10 text-primary border-primary/20"
            >
              {totalTasks} tâche{totalTasks !== 1 ? "s" : ""}
            </Badge>
          </div>
        </CardContent>
      </Card>

      <ProjectDetailDialog
        project={project}
        open={isDetailOpen}
        onOpenChange={setIsDetailOpen}
      />
    </>
  );
};
