import React, { useState } from "react";
import { Project } from "@/app/lib/api";
import { useCreateTask, useUpdateTask, useDeleteTask } from "@/hooks/useTasks";
import { useDeleteProject } from "@/hooks/useProjects";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
} from "@/app/components/ui/dialog";
import { Button } from "@/app/components/ui/button";
import { Input } from "@/app/components/ui/input";
import { Badge } from "@/app/components/ui/badge";
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
} from "@/app/components/ui/card";
import { Label } from "@/app/components/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/app/components/ui/select";
import {
  Tabs,
  TabsContent,
  TabsList,
  TabsTrigger,
} from "@/app/components/ui/tabs";
import {
  CheckCircle,
  Clock,
  Plus,
  Trash2,
  Calendar,
  AlertCircle,
  Filter,
} from "lucide-react";

interface ProjectDetailDialogProps {
  project: Project;
  open: boolean;
  onOpenChange: (open: boolean) => void;
}

export const ProjectDetailDialog: React.FC<ProjectDetailDialogProps> = ({
  project,
  open,
  onOpenChange,
}) => {
  const [newTaskTitle, setNewTaskTitle] = useState("");
  const [newTaskDueDate, setNewTaskDueDate] = useState("");
  const [statusFilter, setStatusFilter] = useState<string>("all");

  const createTaskMutation = useCreateTask();
  const updateTaskMutation = useUpdateTask();
  const deleteTaskMutation = useDeleteTask();
  const deleteProjectMutation = useDeleteProject();

  const handleCreateTask = (e: React.FormEvent) => {
    e.preventDefault();
    if (!newTaskTitle.trim()) return;

    createTaskMutation.mutate(
      {
        title: newTaskTitle.trim(),
        status: "todo",
        dueDate: newTaskDueDate || new Date().toISOString().split("T")[0],
        projectId: project.id,
      },
      {
        onSuccess: () => {
          setNewTaskTitle("");
          setNewTaskDueDate("");
        },
      }
    );
  };

  const handleUpdateTaskStatus = (
    taskId: string,
    status: "todo" | "in-progress" | "completed"
  ) => {
    updateTaskMutation.mutate({ id: taskId, updates: { status } });
  };

  const handleDeleteTask = (taskId: string) => {
    if (confirm("Êtes-vous sûr de vouloir supprimer cette tâche ?")) {
      deleteTaskMutation.mutate(taskId);
    }
  };

  const handleDeleteProject = () => {
    if (
      confirm(
        "Êtes-vous sûr de vouloir supprimer ce projet ? Toutes les tâches seront également supprimées."
      )
    ) {
      deleteProjectMutation.mutate(project.id, {
        onSuccess: () => {
          onOpenChange(false);
        },
      });
    }
  };

  const getStatusIcon = (status: string) => {
    switch (status) {
      case "completed":
        return <CheckCircle className="h-4 w-4 text-green-600" />;
      case "in-progress":
        return <Clock className="h-4 w-4 text-orange-600" />;
      case "todo":
        return <AlertCircle className="h-4 w-4 text-blue-600" />;
      default:
        return null;
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case "completed":
        return "bg-green-100 text-green-800 border-green-200";
      case "in-progress":
        return "bg-orange-100 text-orange-800 border-orange-200";
      case "todo":
        return "bg-blue-100 text-blue-800 border-blue-200";
      default:
        return "bg-slate-100 text-slate-800 border-slate-200";
    }
  };

  const statusLabels = {
    todo: "À faire",
    "in-progress": "En cours",
    completed: "Terminée",
  };

  const filteredTasks =
    statusFilter === "all"
      ? project.tasks
      : project.tasks.filter((task) => task.status === statusFilter);

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <div className="flex items-start justify-between">
            <div>
              <DialogTitle className="text-2xl">{project.title}</DialogTitle>
              <DialogDescription className="mt-2">
                {project.description}
              </DialogDescription>
            </div>
            <Button
              variant="outline"
              size="sm"
              onClick={handleDeleteProject}
              className="text-red-600 hover:text-red-700 hover:border-red-300"
              disabled={deleteProjectMutation.isPending}
            >
              <Trash2 className="h-4 w-4" />
            </Button>
          </div>
        </DialogHeader>

        <Tabs defaultValue="tasks" className="mt-6">
          <TabsList className="grid w-full grid-cols-2">
            <TabsTrigger value="tasks">
              Tâches ({project.tasks.length})
            </TabsTrigger>
            <TabsTrigger value="overview">Vue d'ensemble</TabsTrigger>
          </TabsList>

          <TabsContent value="tasks" className="space-y-6">
            {/* Add New Task */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg flex items-center gap-2">
                  <Plus className="h-5 w-5" />
                  Nouvelle Tâche
                </CardTitle>
              </CardHeader>
              <CardContent>
                <form onSubmit={handleCreateTask} className="space-y-4">
                  <div className="flex gap-4">
                    <div className="flex-1">
                      <Label htmlFor="taskTitle">Titre de la tâche</Label>
                      <Input
                        id="taskTitle"
                        value={newTaskTitle}
                        onChange={(e) => setNewTaskTitle(e.target.value)}
                        placeholder="Titre de la nouvelle tâche"
                        disabled={createTaskMutation.isPending}
                      />
                    </div>
                    <div className="w-48">
                      <Label htmlFor="taskDueDate">Échéance</Label>
                      <Input
                        id="taskDueDate"
                        type="date"
                        value={newTaskDueDate}
                        onChange={(e) => setNewTaskDueDate(e.target.value)}
                        disabled={createTaskMutation.isPending}
                      />
                    </div>
                    <div className="flex items-end">
                      <Button
                        type="submit"
                        disabled={
                          createTaskMutation.isPending || !newTaskTitle.trim()
                        }
                      >
                        Ajouter
                      </Button>
                    </div>
                  </div>
                </form>
              </CardContent>
            </Card>

            {/* Filter */}
            <div className="flex items-center gap-4">
              <div className="flex items-center gap-2">
                <Filter className="h-4 w-4 text-slate-500" />
                <Label htmlFor="statusFilter">Filtrer par statut :</Label>
              </div>
              <Select value={statusFilter} onValueChange={setStatusFilter}>
                <SelectTrigger className="w-48">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">Toutes les tâches</SelectItem>
                  <SelectItem value="todo">À faire</SelectItem>
                  <SelectItem value="in-progress">En cours</SelectItem>
                  <SelectItem value="completed">Terminées</SelectItem>
                </SelectContent>
              </Select>
            </div>

            {/* Tasks List */}
            <div className="space-y-3">
              {filteredTasks.length > 0 ? (
                filteredTasks.map((task) => (
                  <Card key={task.id} className="border border-slate-200">
                    <CardContent className="py-4">
                      <div className="flex items-center justify-between">
                        <div className="flex items-center gap-3 flex-1">
                          {getStatusIcon(task.status)}
                          <div className="flex-1">
                            <h4 className="font-medium text-slate-800">
                              {task.title}
                            </h4>
                            <div className="flex items-center gap-4 mt-1">
                              <div className="flex items-center gap-1 text-sm text-slate-500">
                                <Calendar className="h-3 w-3" />
                                {new Date(task.dueDate).toLocaleDateString(
                                  "fr-FR"
                                )}
                              </div>
                              <Badge
                                variant="outline"
                                className={`text-xs ${getStatusColor(
                                  task.status
                                )}`}
                              >
                                {statusLabels[task.status]}
                              </Badge>
                            </div>
                          </div>
                        </div>
                        <div className="flex items-center gap-2">
                          <Select
                            value={task.status}
                            onValueChange={(status) =>
                              handleUpdateTaskStatus(task.id, status as any)
                            }
                            disabled={updateTaskMutation.isPending}
                          >
                            <SelectTrigger className="w-32">
                              <SelectValue />
                            </SelectTrigger>
                            <SelectContent>
                              <SelectItem value="todo">À faire</SelectItem>
                              <SelectItem value="in-progress">
                                En cours
                              </SelectItem>
                              <SelectItem value="completed">
                                Terminée
                              </SelectItem>
                            </SelectContent>
                          </Select>
                          <Button
                            variant="ghost"
                            size="sm"
                            onClick={() => handleDeleteTask(task.id)}
                            className="text-red-600 hover:text-red-700"
                            disabled={deleteTaskMutation.isPending}
                          >
                            <Trash2 className="h-4 w-4" />
                          </Button>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                ))
              ) : (
                <Card className="border-2 border-dashed border-slate-200 bg-slate-50/50">
                  <CardContent className="py-12 text-center">
                    <AlertCircle className="h-8 w-8 text-slate-400 mx-auto mb-3" />
                    <p className="text-slate-500">
                      {statusFilter === "all"
                        ? "Aucune tâche dans ce projet"
                        : `Aucune tâche ${statusLabels[
                            statusFilter as keyof typeof statusLabels
                          ]?.toLowerCase()}`}
                    </p>
                  </CardContent>
                </Card>
              )}
            </div>
          </TabsContent>

          <TabsContent value="overview" className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <Card>
                <CardHeader>
                  <CardTitle className="text-lg">Statistiques</CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="space-y-2">
                    <div className="flex justify-between">
                      <span className="text-sm text-slate-600">
                        Total des tâches
                      </span>
                      <span className="font-medium">
                        {project.tasks.length}
                      </span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-sm text-slate-600">Terminées</span>
                      <span className="font-medium text-green-600">
                        {
                          project.tasks.filter((t) => t.status === "completed")
                            .length
                        }
                      </span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-sm text-slate-600">En cours</span>
                      <span className="font-medium text-orange-600">
                        {
                          project.tasks.filter(
                            (t) => t.status === "in-progress"
                          ).length
                        }
                      </span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-sm text-slate-600">À faire</span>
                      <span className="font-medium text-blue-600">
                        {
                          project.tasks.filter((t) => t.status === "todo")
                            .length
                        }
                      </span>
                    </div>
                  </div>
                </CardContent>
              </Card>

              <Card className="md:col-span-2">
                <CardHeader>
                  <CardTitle className="text-lg">
                    Informations du projet
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div>
                    <Label className="text-sm font-medium text-slate-600">
                      Créé le
                    </Label>
                    <p className="mt-1">
                      {new Date(project.createdAt).toLocaleDateString("fr-FR", {
                        year: "numeric",
                        month: "long",
                        day: "numeric",
                      })}
                    </p>
                  </div>
                  {project.description && (
                    <div>
                      <Label className="text-sm font-medium text-slate-600">
                        Description
                      </Label>
                      <p className="mt-1 text-slate-800">
                        {project.description}
                      </p>
                    </div>
                  )}
                </CardContent>
              </Card>
            </div>
          </TabsContent>
        </Tabs>
      </DialogContent>
    </Dialog>
  );
};
