"use client";

import React, { useState } from "react";
import { useProjects, useCreateProject } from "@/hooks/useProjects";
import { ProjectCard } from "@/app/components/ProjectCard";
import { CreateProjectDialog } from "@/app/components/CreateProjectDialog";
import { Button } from "@/app/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/app/components/ui/card";
import { Badge } from "@/app/components/ui/badge";
import { Skeleton } from "@/app/components/ui/skeleton";
import {
  FolderKanban,
  Plus,
  TrendingUp,
  CheckCircle,
  Clock,
} from "lucide-react";

const Dashboard = () => {
  const [isCreateDialogOpen, setIsCreateDialogOpen] = useState(false);
  const { data: projects, isLoading, error } = useProjects();

  if (isLoading) {
    return (
      <div className="p-8 space-y-8">
        <div className="space-y-4">
          <Skeleton className="h-8 w-64" />
          <Skeleton className="h-4 w-96" />
        </div>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {[1, 2, 3].map((i) => (
            <Skeleton key={i} className="h-32" />
          ))}
        </div>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {[1, 2, 3, 4, 5, 6].map((i) => (
            <Skeleton key={i} className="h-48" />
          ))}
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="p-8 flex items-center justify-center">
        <Card className="w-full max-w-md">
          <CardHeader>
            <CardTitle className="text-destructive">Erreur</CardTitle>
            <CardDescription>
              Impossible de charger les projets. Veuillez réessayer.
            </CardDescription>
          </CardHeader>
        </Card>
      </div>
    );
  }

  const totalTasks =
    projects?.reduce((acc, project) => acc + project.tasks.length, 0) || 0;
  const completedTasks =
    projects?.reduce(
      (acc, project) =>
        acc +
        project.tasks.filter((task) => task.status === "completed").length,
      0
    ) || 0;
  const inProgressTasks =
    projects?.reduce(
      (acc, project) =>
        acc +
        project.tasks.filter((task) => task.status === "in-progress").length,
      0
    ) || 0;

  return (
    <div className="p-8 space-y-8 bg-gradient-to-br from-slate-50 to-white min-h-screen">
      {/* Header */}
      <div className="space-y-2">
        <h1 className="text-3xl font-bold text-slate-800">Dashboard</h1>
        <p className="text-slate-600">
          Vue d'ensemble de vos projets et tâches
        </p>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <Card className="border-0 shadow-sm bg-gradient-to-br from-blue-50 to-blue-100/50">
          <CardHeader className="pb-3">
            <div className="flex items-center justify-between">
              <CardTitle className="text-lg font-semibold text-blue-800">
                Projets Actifs
              </CardTitle>
              <FolderKanban className="h-5 w-5 text-blue-600" />
            </div>
          </CardHeader>
          <CardContent>
            <div className="text-3xl font-bold text-blue-900">
              {projects?.length || 0}
            </div>
            <p className="text-sm text-blue-700 mt-1">projets en cours</p>
          </CardContent>
        </Card>

        <Card className="border-0 shadow-sm bg-gradient-to-br from-green-50 to-green-100/50">
          <CardHeader className="pb-3">
            <div className="flex items-center justify-between">
              <CardTitle className="text-lg font-semibold text-green-800">
                Tâches Terminées
              </CardTitle>
              <CheckCircle className="h-5 w-5 text-green-600" />
            </div>
          </CardHeader>
          <CardContent>
            <div className="text-3xl font-bold text-green-900">
              {completedTasks}
            </div>
            <p className="text-sm text-green-700 mt-1">
              sur {totalTasks} tâches
            </p>
          </CardContent>
        </Card>

        <Card className="border-0 shadow-sm bg-gradient-to-br from-orange-50 to-orange-100/50">
          <CardHeader className="pb-3">
            <div className="flex items-center justify-between">
              <CardTitle className="text-lg font-semibold text-orange-800">
                En Cours
              </CardTitle>
              <Clock className="h-5 w-5 text-orange-600" />
            </div>
          </CardHeader>
          <CardContent>
            <div className="text-3xl font-bold text-orange-900">
              {inProgressTasks}
            </div>
            <p className="text-sm text-orange-700 mt-1">tâches actives</p>
          </CardContent>
        </Card>
      </div>

      {/* Projects Section */}
      <div className="space-y-6">
        <div className="flex items-center justify-between">
          <div>
            <h2 className="text-2xl font-bold text-slate-800">Vos Projets</h2>
            <p className="text-slate-600 mt-1">
              Gérez vos projets et leurs tâches
            </p>
          </div>
          <Button
            onClick={() => setIsCreateDialogOpen(true)}
            className="gap-2 shadow-sm"
          >
            <Plus className="h-4 w-4" />
            Nouveau Projet
          </Button>
        </div>

        {projects && projects.length > 0 ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {projects.map((project) => (
              <ProjectCard key={project.id} project={project} />
            ))}
          </div>
        ) : (
          <Card className="border-2 border-dashed border-slate-200 bg-slate-50/50">
            <CardContent className="flex flex-col items-center justify-center py-16 px-6">
              <div className="p-4 bg-slate-100 rounded-full mb-4">
                <FolderKanban className="h-8 w-8 text-slate-500" />
              </div>
              <h3 className="text-xl font-semibold text-slate-700 mb-2">
                Aucun projet pour le moment
              </h3>
              <p className="text-slate-500 text-center mb-6 max-w-md">
                Créez votre premier projet pour commencer à organiser vos tâches
                et suivre votre progression.
              </p>
              <Button
                onClick={() => setIsCreateDialogOpen(true)}
                className="gap-2"
              >
                <Plus className="h-4 w-4" />
                Créer mon premier projet
              </Button>
            </CardContent>
          </Card>
        )}
      </div>

      <CreateProjectDialog
        open={isCreateDialogOpen}
        onOpenChange={setIsCreateDialogOpen}
      />
    </div>
  );
};

export default Dashboard;